
.icon[data-category='window'] {
  background-image: url(~assets/icons/window.svg);
}
.grow-enter-active {
  animation: grow-in .5s;
}
.grow-leave-active {
  animation: grow-out .5s;
}
@keyframes grow-in {
0% {
    transform: scale(0.8);
    opacity: 0;
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@keyframes grow-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.8);
    opacity: 0;
}
}
.shrink-enter-active {
  animation: shrink-in .5s;
}
.shrink-leave-active {
  animation: shrink-out .5s;
}
@keyframes shrink-in {
0% {
    transform: scale(1.2);
    opacity: 0;
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@keyframes shrink-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(1.2);
    opacity: 0;
}
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #e3e3e3;
}
html, body, ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}
html {
  font-size: 75%;
}
body {
  font-size: 1.33rem;
  background-color: #0a1c2e;
}



